<template>
  <!--  container-->
  <el-container class="home-container">
    <!---->
    <el-header>
        <div>
          <img src="../assets/shizi.png" alt />
          <span>C5 </span> 
        </div>
       <h6 class="tips">{{this.Manager.mname}}</h6> 
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!-- -->
    <el-container>
      <!-- -->
      <el-aside :width="isCollapsed?'64px':'200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <el-menu

            class="el-menu-vertical-demo"
            
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#50c870"
            
            :collapse="isCollapsed"
            :collapse-transition="false"
            :router="true"
            :default-active="activePath">

          <!--          -->
          <el-submenu :index="item.id+''" v-for="item in menuList" :key="item.id">
            <template slot="title">
              <i :class="iconsObject[item.id]" ></i>
              <span>{{item.title}}</span>
            </template>
<!--            -->
              <el-menu-item :index="it.path" v-for="it in item.subMenuList" :key="it.id" @click="SaveNavState(it.path)">
                <template slot="title">
                  <i :class="iconsObject[it.id]"></i>
                  <span>{{it.title}}</span>
                </template>
              </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!---->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>


export default {
  data(){
    return{
      //
      menuList:[],
      isCollapsed:false,//
      activePath:'/welcome',//
      Manager:[],//
      iconsObject: {
        '100':'iconfont icon-zhanghaoquanxianguanli',
        '200':'iconfont icon-huapen',
        '101':'iconfont icon-s_yonghuliebiao',
        '102':'iconfont icon-quanxian',
        '103':'iconfont icon-shouye',
        '104':'iconfont icon-jiaoyisuo',
        '201':'iconfont icon-jiaoyubaike',
        '202':'iconfont icon-shujuzichan-gerenzichan',
        '203':'iconfont icon-jiaoyisuo',
        '204':'iconfont icon-zhanghaoquanxianguanli',
      },

    }
  },


    //onload
    created(){
      // menuList
      this.getMenuList()
      this.getManager()
      this.activePath=window.sessionStorage.getItem('activePath'); //sessionpath  path
      
    },

    //
    methods:{
        //
        getManager(){
            this.Manager=window.sessionStorage.getItem('user')//
            this.Manager=JSON.parse(this.Manager)
            console.log(this.Manager)
            console.log(typeof(this.Manager))
        },

        // 
        logout(){
            window.sessionStorage.clear();//session
            this.$router.push("/login");//
        },
        //
        async getMenuList(){
          const {data:res}=await this.$http.get("menus");
          // console.log(res);//
          if(res.flag!=200) return this.$message.error("");//
          this.menuList=res.menus;//
        },
        //
        toggleCollapse(){
            this.isCollapsed=!this.isCollapsed;
        },
        //
        SaveNavState(activePath){
          window.sessionStorage.setItem('activePath',activePath);//session
          this.activePath=activePath;
        }
    },
}
</script>
<style lang="less" scoped>
  //
  .home-container{
    height: 100%;

  }
  //
  .el-header{
    background: #373d41;
    display: flex;
    justify-content: space-between;//
    padding-left: 0;//
    align-items: center;
    color:#fff;
    font-size: 30px;
    >div {
      display: flex;
      align-items: center;

      span {
        margin-left: 15px;
      }
    }
  }
  //
  .el-aside{
    background:#333744;
    .el-menu{
      border-right: #333744;
    }
  }
  //
  .el-main{
    background:#eaedf1;
  }
  .el-icon-s-help{
    color: #63a35c;
  }
  img{
    width:55px;
    height:55px;
  }
  // |||
  .toggle-button{
    background: #4A5064;
    font-size: 10px;
    line-height: 24px;
    color: #ffffff;
    text-align: center;//
    letter-spacing: 0.2em;//
    cursor: pointer;//
  }
  .tips{
    padding-left: 50%;
  }
</style>
