<template>
  <div>
    <h2></h2>
<!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
<!--      -->
      <el-card>
        <el-row :gutter="25"> <!---->
<!--        -->
<!--        -->
          <el-col :span="10" >  <!---->
            <el-input placeholder=""  v-model="queryInfo.query" clearable @click="getStockList">
              <el-button slot="append" icon="el-icon-search" @click="getStockList">

              </el-button>
            </el-input>
          </el-col>
          <!-- -->
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible =true"></el-button>
          </el-col>
        </el-row>

        <!--   border   stripe-->
        <el-table :data="StockList" border stripe>

          <el-table-column type="index"></el-table-column> <!---->
          <el-table-column label="" prop="gno"></el-table-column>
          <el-table-column label="" prop="gname"></el-table-column>
          <el-table-column label="" prop="tname"></el-table-column>
          <el-table-column label="" prop="gnum"></el-table-column>
          <el-table-column label="" prop="sno"></el-table-column>
          <el-table-column label="" prop="sname"></el-table-column>
          <el-table-column label="" prop="ssize"></el-table-column>


          <el-table-column label="" >
            <template slot-scope="scope">

              <!--  -->
              <el-tooltip effect="dark" content="" placement="top-start" :enterable="false" >
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog2(scope.row)"></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click= "deleteStock(scope.row.sno,scope.row.gno)"></el-button>
              </el-tooltip>

            </template>
          </el-table-column>

        </el-table>

<!--         size-change  current-change layout-->
        <div>
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pageNum"
              :page-sizes="[1, 2, 5,10 ,20,100]"
              :page-size="queryInfo.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
          </el-pagination>
        </div>
      </el-card>

    <!---->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form  ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="90px">
        <el-form-item label="" prop="whosend">
          <el-input v-model="addForm.whosend"></el-input>
        </el-form-item>
        <el-form-item label="" prop="whoget" >
          <el-input v-model="addForm.whoget"></el-input>
        </el-form-item>
        <el-form-item label="" prop="shipmentplace">
          <el-input v-model="addForm.shipmentplace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="destination">
          <el-input v-model="addForm.destination"></el-input>
        </el-form-item>
          <el-form-item label="" prop="info">
            <el-input v-model="addForm.info"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form  ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="90px">
        <el-form-item label="" prop="whosend">
          <el-input v-model="editForm.whosend" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="whoget" >
          <el-input v-model="editForm.whoget" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="shipmentplace">
          <el-input v-model="editForm.shipmentplace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="destination">
          <el-input v-model="editForm.destination"></el-input>
        </el-form-item>
        <el-form-item label="" prop="info">
          <el-input v-model="editForm.info"></el-input>
        </el-form-item>


      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  //onLoad()
  created() {
    this.getStockList()
  },
  // 
  data(){

    return{

      //
      queryInfo:{
        query:"",//
        pageNum:1,//
        pageSize:5,//
      },
      StockList:[],//
      total:0,//
      addDialogVisible:false,//

      //
      addForm:{
        
      },

      //
      editForm:{},
      ///
      editDialogVisible:false,

      //
      addFormRules: {
        whosend: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 20, message: ' 5  20 ', trigger: 'blur'}
        ],
        whoget: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 20, message: ' 5  20 ', trigger: 'blur'}
        ],
        shipmentplace: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 30, message: ' 6  30 ', trigger: 'blur'}
        ],
        destination: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 30, message: ' 6  30 ', trigger: 'blur'}
        ],
        info: [
          {required: true, message: '', trigger: 'blur'},
          {min: 0, max: 150, message: ' 0  150 ', trigger: 'blur'}
        ],

      },

      //
      editFormRules: {
        whosend: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 20, message: ' 5  20 ', trigger: 'blur'}
        ],
        whoget: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 20, message: ' 5  20 ', trigger: 'blur'}
        ],
        shipmentplace: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 30, message: ' 6  30 ', trigger: 'blur'}
        ],
        destination: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 30, message: ' 6  30 ', trigger: 'blur'}
        ],
        info: [
          {required: true, message: '', trigger: 'blur'},
          {min: 0, max: 150, message: ' 0  150 ', trigger: 'blur'}
        ],
      },
    };
  },

  //
  methods:{



    // 
    async getStockList(){
      const{data:res} =await this.$http.get("getStock",{params:this.queryInfo}) //get
      this.StockList=res.data;//
      this.total=res.numbers;//

    },


    // 
    handleSizeChange(newSize){
      this.queryInfo.pageSize=newSize;
      this.getStockList();
    },

    //pageNum
    handleCurrentChange(newPage){
      this.queryInfo.pageNum=newPage;
      this.getStockList();
    },



    // 
    addDialogClosed(){
        this.$refs.addFormRef.resetFields();
    },

    editDialogClosed(){
      this.$refs.editFormRef.resetFields();
    },


    //
    addUser(){
      this.$refs.addFormRef.validate(async valid=>{
            console.log(valid);
            // 
             if(!valid) return;
             const{data:res}=await this.$http.post("addUser2",this.addForm);
             if(res!="success"){
               return this.$message.error("");
             }
              this.$message.success("");
              this.addDialogVisible = false;
              await this.getUserList();
          });
    },

    //
    async deleteStock(sno,gno){
     const confirmResult =await this.$confirm("",'',{
       confirmButtonText:"",
       cancelButtonClass:"",
       type:'warning'
     }).catch(err=>err)
      if(confirmResult!='confirm'){
        return this.$message.info("")
      }
      const{data:res}=await this.$http.post("deleteStock?sno="+sno+"&gno="+gno);
      console.log(res);
      if(res!="success"){
        return this.$message.error("");
      }
      this.$message.success("");
      await this.getUserList();
    },


    //2
    async showEditDialog2(Info){
      this.editForm=Info;//
      this.editDialogVisible=true;//
    },

   //
  editUser(){
    this.$refs.editFormRef.validate(async valid=>{
      console.log(valid);
      // 
      if(!valid) return;
      const{data:res}=await this.$http.post("editUser2",this.editForm);
      if(res!="success"){
        return this.$message.error("");
      }
      this.$message.success("");
      this.editDialogVisible = false;
      await this.getUserList();

    })
  }
}

};
</script>

<style lang='less' scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size:17px;
}
.el-select .el-input {
  width: 130px;
}

.el-row{

}
</style>