<template>
  <div>
    <h2></h2>
<!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
<!--      -->
      <el-card>
        <el-row :gutter="25"> <!---->
<!--        -->
<!--        -->
          <el-col :span="10" >  <!---->
            <el-input placeholder=""  v-model="queryInfo.query" clearable @click="getinStockList">
              <el-button slot="append" icon="el-icon-search" @click="getinStockList">

              </el-button>
            </el-input>
          </el-col>
          <!-- -->
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible =true"></el-button>
          </el-col>
        </el-row>

        <!--   border   stripe-->
        <el-table :data="inStockList" border:true stripe>

          
          <el-table-column label="" prop="ono"></el-table-column>
          <el-table-column label="" prop="pname"></el-table-column>
          <el-table-column label="" prop="ptel"></el-table-column>
          <el-table-column label="" prop="paddr"></el-table-column>
          <el-table-column label="" prop="gname"></el-table-column>
          <el-table-column label="" prop="innum"></el-table-column>
          <el-table-column label="" prop="indate"></el-table-column>
          <el-table-column label="" prop="inprice"></el-table-column>
          <el-table-column label="" prop="sname"></el-table-column>
     

          <el-table-column label="" >
            <template slot-scope="scope">

              <!--  -->
              <el-tooltip effect="dark" content="" placement="top-start" :enterable="false" >
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip effect="dark" content="" placement="top-start" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click= "deleteInStock(scope.row.ono)"></el-button>
              </el-tooltip>

            </template>
          </el-table-column>

        </el-table>

<!--         size-change  current-change layout-->
        <div>
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryInfo.pageNum"
              :page-sizes="[1, 2, 5,10 ,20,100]"
              :page-size="queryInfo.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
          </el-pagination>
        </div>
      </el-card>

    <!---->
    <el-dialog title="" :visible.sync="addDialogVisible" @close="addDialogClosed" width="30%">
      <el-form  ref="addFormRef" :model="addForm" :rules="addFormRules" label-position="top" size="mini">
        <el-form-item label="" prop="ono">
          <el-input v-model="addForm.ono"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pno" >
          <el-select v-model="addForm.pno" placeholder="">
            <el-option v-for="item in this.ProviderList" :key="item.pno" :label="item.pname" :value="item.pno"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="gno"  >
          <el-select v-model="addForm.gno" placeholder="">
            <el-option v-for="item in this.GoodList" :key="item.gno" :label="item.gname" :value="item.gno"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="innum">
          <el-input  v-model="addForm.innum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="indate">
            <el-date-picker type="date" placeholder="" v-model="addForm.indate" style="width: 100%;" ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="inprice">
            <el-input v-model="addForm.inprice"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addInStock"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" @close="editDialogClosed" width="30%">
      <el-form  ref="editFormRef" :model="editForm" :rules="addFormRules" label-position="top" size="mini">
        <el-form-item label="" prop="ono">
          <el-input v-model="editForm.ono" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="pno" >
          <el-select v-model="editForm.pno" placeholder="">
            <el-option v-for="item in this.ProviderList" :key="item.pno" :label="item.pname" :value="item.pno"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="gno"  >
          <el-select v-model="editForm.gno" placeholder="">
            <el-option v-for="item in this.GoodList" :key="item.gno" :label="item.gname" :value="item.gno"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="innum">
          <el-input v-model="editForm.innum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="indate">
            <el-date-picker type="date" placeholder="" v-model="editForm.indate" style="width: 100%;" ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="inprice">
            <el-input v-model="editForm.inprice"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInStock"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  //onLoad()
  created() {
    this.getGoodList()
    this.getinStockList()
    this.getProviderList()
  },
  // 
  data(){

    return{

      //
      queryInfo:{
        query:"",//
        pageNum:1,//
        pageSize:5,//
      },
      GoodList:[],
      ProviderList:[],
      inStockList:[],//
      total:0,//
      addDialogVisible:false,//

      //
      addForm:{
        ono:"",
        pno:"",
        gno:"",
        inprice:"",
        indate:"",
        ono:""
        
      },

      //
      editForm:{},
      ///
      editDialogVisible:false,

      //
      addFormRules: {
        ono: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 5, message: '5', trigger: 'blur'}
        ],
        innum: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max:4 , message: '4', trigger: 'blur'}
        ],
        inprice: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 4, message: '4', trigger: 'blur'}
        ],
        pno: [
          { required: true, message: '', trigger: 'change' }
        ],
        gno: [
          { required: true, message: '', trigger: 'change' }
        ],
        indate: [
          {type: 'date', required: true, message: '', trigger: 'change' }
        ],


      },

      //
      editFormRules: {
         ono: [
          {required: false, message: '', trigger: 'blur'},
          {min: 1, max: 5, message: '5', trigger: 'blur'}
        ],
        innum: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max:4 , message: '4', trigger: 'blur'}
        ],
        inprice: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 4, message: '4', trigger: 'blur'}
        ],
        pno: [
          { required: true, message: '', trigger: 'change' }
        ],
        gno: [
          { required: true, message: '', trigger: 'change' }
        ],
        indate: [
          {type: 'date', required: true, message: '', trigger: 'change' }
        ],
      },
    };
  },

  //
  methods:{

      // 
    async getProviderList(){
      const{data:res} =await this.$http.get("getProvider",{params:this.queryInfo}) //get
      this.ProviderList=res.data;//

    },

        // 
    async getGoodList(){
      const{data:res} =await this.$http.get("getGood",{params:this.queryInfo}) //get
      this.GoodList=res.data;//

    },

    // 
    async getinStockList(){
      const{data:res} =await this.$http.get("getInStock",{params:this.queryInfo}) //get
      this.inStockList=res.data;//
      this.total=res.numbers;//

    },


    // 
    handleSizeChange(newSize){
      this.queryInfo.pageSize=newSize;
      this.getinStockList();
    },

    //pageNum
    handleCurrentChange(newPage){
      this.queryInfo.pageNum=newPage;
      this.getinStockList();
    },



    // 
    addDialogClosed(){
        this.$refs.addFormRef.resetFields();
    },

    editDialogClosed(){
      this.$refs.editFormRef.resetFields();
    },


    //
    addInStock(){
      this.$refs.addFormRef.validate(async valid=>{
            console.log(valid);
            // 
             if(!valid) return;
             const{data:res}=await this.$http.post("addInStock",this.addForm);
             if(res!="success"){
               return this.$message.error("");
             }
              this.$message.success("");
              this.addDialogVisible = false;
              await this.getInStockList();
          });
    },

    //
    async deleteInStock(ono){
     const confirmResult =await this.$confirm("",'',{
       confirmButtonText:"",
       cancelButtonClass:"",
       type:'warning'
     }).catch(err=>err)
      if(confirmResult!='confirm'){
        return this.$message.info("")
      }
      const{data:res}=await this.$http.post("deleteInStock?ono="+ono);
      console.log(res);
      if(res!="success"){
        return this.$message.error("");
      }
      this.$message.success("");
      await this.getUserList();
    },


    //
    async showEditDialog(Info){
      
      this.editForm=Info;//
      this.editDialogVisible=true;//
    },

   //
  editInStock(){
    this.$refs.editFormRef.validate(async valid=>{
      console.log(valid);
      // 
      if(!valid) return;
      const{data:res}=await this.$http.post("editInStock",this.editForm);
      if(res!="success"){
        return this.$message.error("");
      }
      this.$message.success("");
      this.editDialogVisible = false;
      await this.getUserList();

    })
  }
}

};
</script>

<style lang='less' scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size:17px;
}
.el-select .el-input {
  width: 130px;
}

.el-row{

}
</style>