<template>
    <div class="login_container">
        <!--  -->
        <div class="login_box">
            <!--  -->
            <div class="avatar_box">
                <img src="../assets/shizi.png" alt/>
            </div> 
            <!--  -->
            <el-form ref="loginFormRef"  :rules="loginRules" :model="LoginForm" class ="login_form" label-width="0">
                <!--  -->
                <el-form-item prop="mno">
                    <el-input v-model="LoginForm.mno" prefix-icon="iconfont icon-denglu"></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item prop="mpw">
                    <el-input v-model="LoginForm.mpw" prefix-icon="iconfont icon-mima" show-password></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login()"></el-button>
                    <el-button type="info" @click="resetLoginFrom()"></el-button>       
                </el-form-item>
            </el-form>
        </div>      
    </div>
</template>
<script>
export default {
    data(){
        return{
            //
            LoginForm:{

                mpw:"",
                mno:"",
                mname:""
            },
            //
            loginRules:{
                //
                mno: [
                    { required: true, message: '', trigger: 'blur' },// 
                    { min: 5, max: 12, message: ' 5  12 ', trigger: 'blur' } //
                ],  
                //
                mpw: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, max: 10, message: ' 6  10 ', trigger: 'blur' }
                ],  
            },
        }; 
    },
    methods:{
        //
        resetLoginFrom(){
            this.$refs.loginFormRef.resetFields();
        },
        //
        login(){
            //1.
            this.$refs.loginFormRef.validate(async valid =>{
                if(!valid) return;// 
                console.log(this.LoginForm)
                const {data:res}=await this.$http.post("login",this.LoginForm);//
                if(res.flag =="ok"){
                    this.$message.success("");//
                //console.log(res.trueUser);//
                    window.sessionStorage.setItem("user",JSON.stringify(res.manager));//
                    this.$router.push({path:"/home"});//
                }else{
                    this.$message.error("");//
                }
            });
        },

    },
}
</script>
<style lang="less"  scoped>
//
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;// 
  position: absolute;// 
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);//  

  .avatar_box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;//
    border-radius: 50%; // 
    padding: 2px; //
    box-shadow: 0 0 10px #0ee;// 
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #0ee;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%; // 
      background-color: rgb(255, 255, 255);
    }
  }
}
.btns{
    display: flex;
    justify-content: flex-end;
}.login_form{
    position:absolute;
    bottom: 0%;
    width: 100%;
    padding:0 10px; //10
    box-sizing:border-box;//
}

</style>